.slide {
  position: absolute;
  top: 37vw;
  left: 50%;
  width: 1rem;
  height: 1rem;
  cursor: pointer;
  border: none;
  opacity: 0.2;
  transition: all 500ms ease-out;
}
.slide:after {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 0.6rem;
  top: 0;
  left: 0;
  position: relative;
  /* background: rgba(0, 0, 0, 0.7); */
  content: "";
  display: inline-block;
  visibility: visible;
  z-index: 10;
}

.slide:checked:after {
  width: 0.9rem;
  height: 0.9rem;
  border-radius: 0.5rem;
  top: 0;
  left: 0;
  position: relative;
  background-color: #989cff;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 1px solid #989cff;
}
.slide:checked {
  opacity: 1;
}
#s1 {
  left: 47%;
